import { createSlice } from '@reduxjs/toolkit';

export const counterSlice = createSlice({
  name: 'modal',
  initialState: {
    isContentModalOpen: false,
    contentUrl: {
      link: '',
      key: '',
      id: '',
    },
  },

  reducers: {
    openModal: (state) => {
      state.isContentModalOpen = true;
    },
    closeModal: (state) => {
      state.isContentModalOpen = false;
    },
    setContentUrl: (state, action) => {
      state.contentUrl = action.payload;
    },
  },
});

export const { openModal, closeModal, setContentUrl } = counterSlice.actions;

export default counterSlice.reducer;
