import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import { styles } from './styles';
import { NAVIGATION_LINKS } from '../../navigation/navigationLinks';
import { LinkButton } from '../../components';
import { closeModal, openModal, setContentUrl } from '../../store/reducers/modal';
import { connect } from 'react-redux';

HeaderMenu.propTypes = {};

const useStyles = makeStyles(styles);

function HeaderMenu(props) {
  const classes = useStyles();
  const { setContent, openModal } = props;

  const openUrl = (e, itemObj) => {
    e.preventDefault();

    setContent({ link: itemObj.link, key: itemObj.key, id: itemObj.key });
    openModal();
  };

  const renderLink = (text, itemObj, innerLink = false) => {
    return (
      <div className={classes.menuItemWrapper}>
        <LinkButton
          linkClass={classes.menuItem}
          url={itemObj.link}
          {...(innerLink ? { onClick: (e) => openUrl(e, itemObj) } : {})}
          innerLink={innerLink}
        >
          {text}
        </LinkButton>
      </div>
    );
  };

  return (
    <div className={classes.root}>
      <div className={classes.headerTitleWrapper}>
        <LinkButton url={NAVIGATION_LINKS.HOME} innerLink>
          <span className={classes.headerTitle}>Andre Bozhenko</span>
        </LinkButton>
      </div>
      <div className={classes.navigationMenuWrapper}>
        {renderLink('About', { key: 'about', link: NAVIGATION_LINKS.ABOUT }, true)}
        {renderLink('Dribbble', { key: '', link: 'https://google.com' })}
        {renderLink('Instagram', { key: '', link: '/' })}
        {renderLink('Facebook', { key: '', link: '/' })}
      </div>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    modal: state.modal,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    openModal: () => dispatch(openModal()),
    closeModal: () => dispatch(closeModal()),
    setContent: (contentUrl) => dispatch(setContentUrl(contentUrl)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HeaderMenu);
