import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import { Typography } from '@material-ui/core';
import { COLORS, TRANSITIONS } from '../../helpers';

GalleryItem.propTypes = {
  item: PropTypes.object.isRequired,
  onOpen: PropTypes.func,
  setContent: PropTypes.func,
};

const useStyles = makeStyles((theme) => ({
  itemWrapper: {
    width: '20%',
    height: '20vw',
    overflow: 'hidden',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column',
    position: 'relative',
    textDecoration: 'none',
    outline: 'none',
    [theme.breakpoints.down('lg')]: {
      width: '25%',
      height: '25vw',
    },
    [theme.breakpoints.down('md')]: {
      width: '33.3333333%',
      height: '33.3333333vw',
    },
    [theme.breakpoints.down('sm')]: {
      width: '50%',
      height: '50vw',
    },
    [theme.breakpoints.down('xs')]: {
      width: '100%',
      height: '100vw',
    },
    '&:hover': {
      textDecoration: 'none',
    },
    '&:hover > img': {
      opacity: 0.5,
      transform: 'scale(1.5)',
    },
    '&:hover > div': {
      opacity: 1,
      backgroundColor: 'rgba(0,0,0,0.12)',
      transform: 'scale(1)',
    },
  },
  itemImage: {
    position: 'absolute',
    top: 'auto',
    left: 'auto',
    height: 'auto',
    minWidth: '100%',
    minHeight: '100%',
    opacity: 1,
    transition: TRANSITIONS.galleryItem,
    zIndex: -1,
    width: '100%',
  },
  itemContent: {
    width: '100%',
    height: '100%',
    opacity: 0,
    backgroundColor: 'rgba(0,0,0,0)',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    transition: TRANSITIONS.galleryItem,
    transform: 'scale(1.5)',
  },
  itemTitle: {
    maxWidth: '90%',
    fontSize: 50,
    fontWeight: 600,
    color: COLORS.black,
    userSelect: 'none',
  },
  itemSubTitle: {
    maxWidth: '90%',
    fontSize: 22,
    color: COLORS.black,
    userSelect: 'none',
    textAlign: 'center',
  },
}));

function GalleryItem(props) {
  const classes = useStyles();

  const { item, onOpen, setContent } = props;

  const openUrl = (e, itemObj) => {
    e.preventDefault();

    setContent({ link: itemObj.link, key: itemObj.id, id: itemObj.id });
    onOpen();
  };

  return (
    <Link className={classes.itemWrapper} href={item.link} onClick={(e) => openUrl(e, item)}>
      <img src={item.image} alt={item.title} className={classes.itemImage} />
      <div className={classes.itemContent}>
        <Typography variant="h2" component="h2" noWrap gutterBottom className={classes.itemTitle}>
          {item.title}
        </Typography>
        <Typography variant="h6" component="p" gutterBottom className={classes.itemSubTitle}>
          {item.subTitle}
        </Typography>
      </div>
    </Link>
  );
}

export default GalleryItem;
