import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import { styles } from './styles';
import { connect } from 'react-redux';
import { closeModal, openModal, setContentUrl } from '../../store/reducers/modal';
import Fade from '@material-ui/core/Fade';
import Backdrop from '@material-ui/core/Backdrop';
import { ContentItem } from '../index';
import { getPortfolioItem } from '../../helpers/portfolio';
import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles(styles);

ContentModal.propTypes = { open: PropTypes.bool, closeModal: PropTypes.func };
ContentModal.defaultProps = {
  open: true,
};

function ContentModal(props) {
  const classes = useStyles();
  const { modal, closeModal, setContentUrl } = props;
  const open = modal.isContentModalOpen;
  const contentToGet = modal.contentUrl;
  const [item, setItem] = useState('');

  useEffect(() => {
    if (!!contentToGet && contentToGet !== '/') {
      setItem(getPortfolioItem(contentToGet.key));
      window.history.pushState(`${contentToGet.id}`, `${contentToGet.key}`, `${contentToGet.link}`);
    } else {
      setItem(getPortfolioItem('404'));
      window.history.pushState(contentToGet, contentToGet, `/`);
    }
  }, [contentToGet]);

  const onCloseModal = () => {
    closeModal();
    window.history.pushState(contentToGet, contentToGet, `/`);
  };

  const resetUrlStatus = () => {
    setContentUrl('/');
  };

  return (
    <Modal
      open={open}
      onClose={onCloseModal}
      aria-labelledby="transition-modal-title"
      aria-describedby="transition-modal-description"
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
      }}
    >
      <Fade in={open} onExited={resetUrlStatus}>
        <div
          className={classes.paper}
          style={{
            top: 0,
            right: 0,
          }}
        >
          <div className={classes.contentRoot}>
            {onCloseModal && (
              <div className={classes.closeModalWrapper}>
                <CloseIcon onClick={onCloseModal} className={classes.closeModalIcon} />
              </div>
            )}
            <ContentItem item={item} />
          </div>
        </div>
      </Fade>
    </Modal>
  );
}

const mapStateToProps = (state) => {
  return {
    modal: state.modal,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    openModal: () => dispatch(openModal()),
    closeModal: () => dispatch(closeModal()),
    setContentUrl: (contentUrl) => dispatch(setContentUrl(contentUrl)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ContentModal);
