import React from 'react';
import PropTypes from 'prop-types';
import { CONTENT_TYPES } from '../../helpers';
import { makeStyles } from '@material-ui/core/styles';
import { styles } from './styles';
import { Typography } from '@material-ui/core';
import Link from '@material-ui/core/Link';

ContentItem.propTypes = {
  item: PropTypes.object.isRequired,
};

const useStyles = makeStyles(styles);

function ContentItem(props) {
  const classes = useStyles();
  const { item } = props;

  const contentDrawer = (content) => {
    let contentToDraw;

    switch (content.type) {
      case CONTENT_TYPES.TITLE: {
        contentToDraw = (
          <Typography variant="h2" component="h2" gutterBottom className={classes.subTitle}>
            {content.content}
          </Typography>
        );
        break;
      }
      case CONTENT_TYPES.TEXT: {
        contentToDraw = (
          <Typography variant="body1" component="p" gutterBottom className={classes.paragraph}>
            {content.content}
          </Typography>
        );
        break;
      }
      case CONTENT_TYPES.IMAGE: {
        contentToDraw = (
          <div className={classes.imageWrapper}>
            <img className={classes.image} src={content.content} alt={content.content} />
          </div>
        );
        break;
      }
      default: {
        contentToDraw = (
          <Typography variant="body1" component="p" gutterBottom className={classes.paragraph}>
            {content.content}
          </Typography>
        );
      }
    }

    return contentToDraw;
  };

  const generateContent = () => {
    let contentToPrint = [];

    if (item && item.content && item.content.length) {
      if (Array.isArray(item.content)) {
        contentToPrint = item.content.map((content, index) => {
          return <div key={`id-${index}`}>{contentDrawer(content)}</div>;
        });
      }
      if (typeof item.content === 'string') {
        contentToPrint = [contentDrawer(item.content)];
      }
    }
    return contentToPrint;
  };

  const drawAdditionalByKey = (key) => {
    return (
      <>
        {!!key && !!item && !!item[key] && (
          <Typography variant="h4" component="p" gutterBottom className={classes.additional}>
            {getAdditionalText(key)}
            {item[key]}
          </Typography>
        )}
      </>
    );
  };

  const drawAdditionalLinkByKey = (key) => {
    return (
      <>
        {!!item && item[key] && (
          <Typography variant="h4" component="p" gutterBottom className={classes.additional}>
            {getAdditionalText(key)}

            {Array.isArray(item[key]) ? item[key].map((link) => drawLink(link)) : drawLink(item[key])}
          </Typography>
        )}
      </>
    );
  };

  const drawLink = (link) => {
    return (
      <Link
        component="a"
        variant="body2"
        href={link}
        target={'_blank'}
        underline={'none'}
        classes={{
          root: classes.outerLink,
        }}
      >
        {link}
      </Link>
    );
  };

  const getAdditionalText = (key) => {
    switch (key) {
      case 'date': {
        return 'Date: ';
      }
      case 'clientName': {
        return 'Client: ';
      }
      case 'role': {
        return 'Role:\n\r';
      }
      case 'outerLink': {
        return 'Link: ';
      }
      default: {
        return '';
      }
    }
  };

  return (
    <>
      {!!item && item.title && (
        <Typography variant="h1" component="h1" gutterBottom className={classes.title}>
          {item.title}
        </Typography>
      )}

      <div className={classes.itemInfoWrapper}>
        <div className={classes.itemInfoMain}>
          {!!item && item.description && (
            <Typography variant="h4" component="p" gutterBottom className={classes.description}>
              {item.description}
            </Typography>
          )}
        </div>

        <div className={classes.itemInfoAdditional}>
          {drawAdditionalByKey('date')}
          {drawAdditionalByKey('clientName')}
          {drawAdditionalByKey('role')}
          {drawAdditionalLinkByKey('outerLink')}
        </div>
      </div>
      {generateContent()}
    </>
  );
}

export default ContentItem;
