import { CONTENT_TYPES } from '../helpers';

export class GalleryItemEntity {
  constructor(props) {
    this.title = props.title || 'Portfolio case cannot be found';
    this.description = props.description || '';
    this.subTitle = props.subTitle || '';
    this.link = props.link || '';
    this.id = props.id || props.link || '';
    this.image = props.image || '';
    this.outerLink = props.outerLink || '';
    this.date = props.date || '';
    this.clientName = props.clientName || '';
    this.role = props.role || '';
    this.content = props.content || [{ type: CONTENT_TYPES.TITLE, content: '404', style: '404' }];
  }

  getContent = () => {
    return this.content || '';
  };
}
