export const getParam = (paramName, match) => {
  if (!match && !match.params) return undefined;
  return match.params[paramName];
};

export const getParams = (match) => {
  let params = {};
  if (!match && !match.params) return params;

  const paramsEntries = Object.entries(match.params);

  paramsEntries.forEach((entry) => {
    params[entry[0]] = entry[1];
  });

  return params;
};
