import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import { ALL_GALLERY_WORKS } from '../../portfolioItems';
import { openModal, closeModal, setContentUrl } from '../../store/reducers/modal';

import { styles } from './styles';
import { GalleryItem } from '../index';
import { connect } from 'react-redux';

PlateGallery.propTypes = {};
const useStyles = makeStyles(styles);

function PlateGallery(props) {
  const classes = useStyles();
  const { openModal, setContentUrl } = props;

  return (
    <div className={classes.root}>
      <div className={classes.galleryWrapper}>
        {ALL_GALLERY_WORKS.map((item) => (
          <GalleryItem key={item.id} item={item} onOpen={openModal} setContent={setContentUrl} />
        ))}
      </div>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    modal: state.modal,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    openModal: () => dispatch(openModal()),
    closeModal: () => dispatch(closeModal()),
    setContentUrl: (contentUrl) => dispatch(setContentUrl(contentUrl)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PlateGallery);
