import { CONTENT_TYPES } from './constants';
import { ALL_ITEMS_BY_LINK } from '../portfolioItems';

export const getPortfolioItem = (itemName) => {
  if (ALL_ITEMS_BY_LINK[itemName]) {
    return ALL_ITEMS_BY_LINK[itemName];
  } else {
    return {
      title: 'Portfolio case cannot be found',
      content: [{ type: CONTENT_TYPES.TITLE, content: '404', style: '404' }],
    };
  }
};
